import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import { Autoplay, Pagination, Navigation, Keyboard } from "swiper/modules";
import * as styles from "./CardStackSlider.module.css";

const GooglePhotosAlbumSlider = ({ list }) => {
    return (
        <Swiper
            spaceBetween={30}
            centeredSlides={true}
            autoplay={{ delay: 2000, disableOnInteraction: false }}
            pagination={{ clickable: true }}
            navigation={true}
            keyboard={{ enabled: true, onlyInViewport: true }}
            modules={[Autoplay, Pagination, Navigation, Keyboard]}
            className={styles.swiperContainer}
        >
            {list.map((album, index) => (
                <SwiperSlide key={index}>
                    <div className={styles.swiperSlideWrapper}>
                        <h2 className={styles.albumTitle}>{album.title}</h2>
                        <img
                            src={album.cover}   // use original URL directly
                            alt={album.title}
                            loading="lazy"
                            className={styles.originalImage}
                            onClick={() => window.open(album.url, "_blank")}
                        />
                    </div>
                </SwiperSlide>
            ))}
        </Swiper>

    );
};

const normalizeGooglePhotoUrl = (url, width = 1000) => {
    if (!url) return "";
    try {
        let base = url.split("?")[0]; // remove query parameters
        base = base.replace(/=w\d+/, `=w${width}`); // replace width if exists
        return base;
    } catch (err) {
        console.warn("Invalid URL:", url);
        return url;
    }
};


export { GooglePhotosAlbumSlider };
